import React, { useEffect, useState } from "react";
import Header from './header';
import {Game} from "./config";
import {numGame} from "./config";
import {addText} from "./config";

window.recordTime = 0;
window.numGame = 0;
window.addTime = [];

const valueExample = Game[numGame-1].valueExample;
const decSymbol = Game[numGame-1].decSymbol;
const rangePlus1 = Game[numGame-1].rangePlus1;
const rangePlus2 = Game[numGame-1].rangePlus2;
const rangeMulti1 = Game[numGame-1].rangeMulti1;
const rangeMulti2 = Game[numGame-1].rangeMulti2;
const rangeDiv1 = Game[numGame-1].rangeDiv1;
const rangeDiv2 = Game[numGame-1].rangeDiv2;


function randomNumberInRange(min, max, decSymbol) {
	return Math.floor(Math.random() * (max - min + 1))/decSymbol + min*decSymbol;
}

function createExample(numEx, decSymbol) {
	const ops = Game[numGame-1].ops;
	const num = Game[numGame-1].id;
	const randomOps = ops[Math.floor(Math.random()*ops.length)]; 

	if ((num === 8) && ((numEx%2) === 0)) {
		decSymbol = 1;		
	}
	
	if (randomOps === '+' || randomOps === '-') {
		const num1 = randomNumberInRange(rangePlus1.min, rangePlus1.max, decSymbol);
		const num2 = randomNumberInRange(rangePlus2.min, rangePlus2.max, decSymbol);
		if (num1 > num2) {
			return num1+randomOps+num2
		} else {
			return num2+randomOps+num1
		}
	}
	if (randomOps === '*') {
		const num1 = randomNumberInRange(rangeMulti1.min, rangeMulti1.max, decSymbol);
		const num2 = randomNumberInRange(rangeMulti2.min, rangeMulti2.max, decSymbol);
		return num1+randomOps+num2
	}
	if (randomOps === '/') {
		const num1 = randomNumberInRange(rangeDiv1.min, rangeDiv1.max, decSymbol);
		const num2 = randomNumberInRange(rangeDiv2.min, rangeDiv2.max, decSymbol);
		const dev = num1*num2;
		return dev+randomOps+num2
	}
}

function App() {

	const [state, setState] = useState({
		textExample: createExample(valueExample, decSymbol),
		numExample: valueExample,
		result: '',
		wishText: Game[numGame-1].name,
		startButton: 'Старт',
		buttonDisable: true,
		numGame: window.numGame
	});

	const textResult = eval(state.textExample).toFixed(2).toString();
		
	//Тамйер
	const [seconds, setSeconds] = useState(0);

	useEffect(() => {
		if (state.startButton === 'Стоп') {
			setTimeout(() => setSeconds(seconds + 1), 1000);
		} else {
			setSeconds(0)
		};
	}, [state.startButton, seconds]);
		
	//Цифрова клавіатура
	const createDigits = () => {
		const digits = [];

		for (let i = 1; i < 10; i++) {
			digits.push(
				<button onClick={() => updateResult(i.toString())} key={i} disabled={ state.buttonDisable }>
					{i}
				</button>
			)
		}
		return digits;
	}

	const deleteLast = () => {
		if (state.result === '') {
			return;
		}
		const value = state.result.slice(0, -1);
		setState({
			...state,			
			result: value
		})
	}

	const updateResult = value => {
		setState({
			...state,			
			result: state.result+value
		})

		const result = state.result+value;
		if (result !== '' && parseFloat(result) === parseFloat(textResult)) {
			setState({
				...state,
				textExample: createExample(state.numExample, decSymbol),
				numExample: state.numExample - 1,
				result: '',
				numGame: state.numGame+1
			})
		}		
	}
	
	//Коли натискаємо на Старт/Стоп
	const calculate = () => {
		if (state.startButton === 'Старт') {
			setState({
				...state,
				textExample: createExample(state.numExample, decSymbol),
				result: '',
				wishText: '',
				startButton: 'Стоп',
				buttonDisable: false
			})
			setSeconds(0);
		} else {
			setState({
				...state,
				numExample: valueExample,
				startButton: 'Старт',
				buttonDisable: true
			})
			setSeconds(0);
		}
	}

	//ФУнкція виводу додаткового повідомлення
	function addTextOnScreen(array_res) {
		const minEl = Math.min.apply(null, array_res.slice(0, array_res.length-2));
		const maxEl = Math.max.apply(null, array_res.slice(0, array_res.length-2));
		const lastEl = array_res[array_res.length - 1];

		if (state.wishText === Game[numGame-1].name) {
			return addText[0]
		}
		if (state.numGame === valueExample) {
			return addText[1]
		}
		if (minEl < lastEl) {
			return addText[3]
		}
		if (maxEl === lastEl) {
			return addText[3]
		}
		if (minEl > lastEl) {
			return addText[2]
		}		
	}

	//Якщо всі завдання скінчилися
	if (state.numExample === parseInt('0')) {		
		setState({
			...state,
			numExample: valueExample,
			result: seconds+' c',
			wishText: 'Ваш час: ',
			buttonDisable: true,
			startButton: 'Старт',			
		});

		window.addTime.push(seconds);
		
		if ((seconds < window.recordTime) || (window.recordTime === 0)) {
			window.recordTime = seconds;			
		}
		setSeconds(0);
	}

	return (
		<div className="App">
			<Header />
			<div className="calculator">
				<div className="head">
					<button className="example">{ state.numExample }</button>
					<button className="timeEx">{ seconds }</button>
					<button className="record">{ window.recordTime }</button>
				</div>
				<div className="head-text">
					<div>Приклад</div>
					<div>Час</div>
					<div>Рекорд</div>
				</div>

				<div className="display">
					<span className="displayTitle">{ state.wishText ? state.wishText : state.textExample+'=' }</ span>
					<span>{ state.result }</span>
					{ state.wishText ? <span className="addText"><br />{ addTextOnScreen(window.addTime) }</span> : '' }
				</div>

				<div className="digits">
					{ createDigits() }
					<button onClick={() => updateResult('0')} disabled={ state.buttonDisable }>0</button>
					<button onClick={() => updateResult('.')} disabled={ state.buttonDisable }>.</button>
					<button onClick={deleteLast} disabled={ state.buttonDisable }>
						<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-backspace-fill" viewBox="0 0 16 16">
						<path d="M15.683 3a2 2 0 0 0-2-2h-7.08a2 2 0 0 0-1.519.698L.241 7.35a1 1 0 0 0 0 1.302l4.843 5.65A2 2 0 0 0 6.603 15h7.08a2 2 0 0 0 2-2V3zM5.829 5.854a.5.5 0 1 1 .707-.708l2.147 2.147 2.146-2.147a.5.5 0 1 1 .707.708L9.39 8l2.146 2.146a.5.5 0 0 1-.707.708L8.683 8.707l-2.147 2.147a.5.5 0 0 1-.707-.708L7.976 8 5.829 5.854z"/>
						</svg>
					</button>
					<button id="operators" onClick={calculate}>{ state.startButton }</button>
				</div>
			</div>
		</div>
	);
}

export default App; 
